import React, { Component } from 'react';
import '../App.css';


let dialogStyles = {
    maxWidth: '100%',
    margin: '0 auto',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%,-50%)',
    zIndex: '999',
    padding: '10px 40px 100px',
    display: 'flex',
    flexDirection: 'column',
    position: 'fixed',
    width: '60rem',
    backgroundColor: '#67837b',
    borderRadius: '15px',
    color: '#ffffff',
    marginRight: '25%',
    overflow: 'auto',
    height: '50%',
};

let dialogCloseButtonStyles = {
    marginBottom: '15px',
    padding: '3px 8px',
    cursor: 'pointer',
    borderRadius: '50%',
    border: 'none',
    width: '30px',
    height: '30px',
    fontWeight: 'bold',
    alignSelf: 'flex-end'
    
};


class Dialog extends Component {
    render() {
        let dialog = (
            <div style={dialogStyles}>
                <button style={dialogCloseButtonStyles} onClick={this.props.onClose}>x</button>

                <div>{this.props.children}</div>
            </div>
        );

        if (! this.props.isOpen) {
            dialog = null;
        }
        return (
            <div>
                {dialog}
            </div>
        );
    }
}


export default Dialog;
